define('app/models/reports/info/info_list',['require','jquery','underscore','backbone'],function(require) {
    var $ = require('jquery'),
        _ = require('underscore'),
        Backbone = require('backbone');

    return Backbone.Model.extend({
        initialize: function (options) {
            this.queryString = options.queryString;
            this.programId = options.programId;
            this.reportId = options.reportId;
            this.infoId = options.infoId;
        },
        url: function () {
            return '/programs/' + this.programId + "/reports/" + this.reportId + "/info/" + this.infoId + ".json?" + this.queryString;
        }
    });
});
