define('app/views/store_profile/main',['require','slick_carousel','context','underscore','jquery','app/views/activities/activities','app/views/store_profile/personnel','app/views/utils/expandWrapperView','app/views/store_profile/openAlerts','app/views/store_profile/resolvedAlerts'],function (require) {
    require('slick_carousel');
    var context = require('context'),
        _ = require('underscore'),
        $ = require('jquery'),
        ActivitiesView = require('app/views/activities/activities'),
        PersonnelSectionView = require('app/views/store_profile/personnel'),
        ExpandWrapperView = require('app/views/utils/expandWrapperView'),
        OpenAlertsView = require('app/views/store_profile/openAlerts'),
        ResolvedAlertsView = require('app/views/store_profile/resolvedAlerts');

    /**
     * The main entry point for loading the JS needed for the store profile page
     *
     * 
     * @exports app/views/store_profile/main
     */
    var main = {
        init: function () {
            _.extend(context, window.bootstrap);
            new PersonnelSectionView().fetch();//render();
            new OpenAlertsView().fetch();//render();
            var wrapper = new ExpandWrapperView();
            wrapper.setElement('#site-canvas').render();
            
            $('#images .image-container').slick({
                slidesToShow: 3,
                slidesToScroll: 1,
                centerMode: true,
                focusOnSelect: true,
                variableWidth: true
            });
        },
        activity: function () {
            var url = '/programs/' + context.programId + '/activities/posts?customer_store=' +context.requestParameters[1] ,
                activitiesView = new ActivitiesView({
                    url: url,
                    programId: context.programId                        
                });
            activitiesView.fetch();
        },
        history: function () {
            _.extend(context, window.bootstrap);
            new OpenAlertsView().renderCollection(context.alerts.open);
            new ResolvedAlertsView().renderCollection(context.alerts.resolved);
        }
    };
    return main;
});
